import React from 'react';
import { Grid, Stack, Typography } from '@mui/material';


export const ImgInfo = ({titulo, texto, img, alt = 'Imagen', primeroTexto = true} : 
{titulo : string, texto : string, img : string, alt : string, primeroTexto : boolean}) => {


  return (
    <Grid container direction={primeroTexto ? 'row' : 'row-reverse'} marginTop='24px'>
        <Grid item xs={12} md={6}>
            <Stack spacing={2} p={2}>
                <Typography variant='h6'>
                    {titulo}
                </Typography>
                <Typography variant='caption' sx={{textAlign: 'justify', whiteSpace: 'pre-line'}} paragraph >
                    {texto}
                </Typography>
            </Stack>
        </Grid>
        <Grid item xs={12} md={6}>
            <img 
                src={img}
                alt={alt}
                style={{
                    maxWidth: '70%',
                    margin: 'auto',
                    textAlign: 'center',
                    display: 'block'
                }}
            ></img>
        </Grid>
    </Grid>
  )
}
