import { Grid, Stack, Typography, Accordion, AccordionSummary, AccordionDetails, Divider, Skeleton, Box } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { Departamento, listaDeptos } from '../../models/Departamento'

import MailIcon from '@mui/icons-material/Mail';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import BuildIcon from '@mui/icons-material/Build';
import EngineeringIcon from '@mui/icons-material/Engineering';
import LocalLibraryIcon from '@mui/icons-material/LocalLibrary';
import { useParams } from 'react-router-dom';


export const MuniDeptoPresentation = () => {

    const [departamento, setDepartamento] = useState<Departamento  | undefined>();
    const deptoID = useParams().deptoID;

    useEffect(() => {
        const depto = listaDeptos.find(depto => depto.abreviatura === deptoID);
        if(depto === undefined){
            return;
        }
        setTimeout(()=> setDepartamento(depto), 1000);
    }, [])
    

    if(!!!departamento){
        return (
            <Stack  sx={{width: '1200px', maxWidth: '95%', margin: '12px auto'}} spacing={2} alignItems='center'>
                <Skeleton variant="rectangular" width='80%' height='56px' />                
                <Skeleton variant="rectangular" width='100%' height='24px' />          
                <Skeleton variant="rectangular" width='100%' height='24px' />          
                <Skeleton variant="rectangular" width='100%' height='24px' />
                <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>                           
                        <Skeleton variant="rectangular" width='500px' height='300px' sx={{maxWidth: '70%', margin: 'auto'}}/>  
                    </Grid>
                    <Grid item xs={12} md={6} >          
                        <Stack spacing={2} p='12px'>
                            <Skeleton variant="rectangular" width='100%' height='48px' /> 
                            <Skeleton variant="rectangular" width='100%' height='48px' />                         
                            <Skeleton variant="rectangular" width='100%' height='48px' />  
                        </Stack>                
                    </Grid>
                </Grid>
            </Stack>
        )
    }

    return (
        <Stack sx={{width: '1200px', maxWidth: '95%', margin: 'auto'}}>
            <Typography variant='h4' color='primary' sx={{textAlign: 'center', fontWeight: 'bold', margin: '12px 0'}}>{departamento.nombre}</Typography>
        
            <Typography variant='caption' sx={{textAlign: 'justify'}} paragraph >
                {departamento!.descripcion}
            </Typography>         
        
            <Grid container spacing={2}>

                <Grid item xs={12} md={6}>
                    <img 
                        src={departamento!.img}
                        alt={departamento!.nombre}
                        style={{
                            maxWidth: '70%',
                            margin: 'auto',
                            display: 'block'
                        }}
                    ></img>                
                </Grid>

                <Grid item xs={12} md={6}>        
                    <Stack>

                        <Accordion elevation={8} sx={{margin: '6px 0'}}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1a-content"
                                id="panel1a-header"
                            >
                                <Stack direction='row' spacing={2} sx={{alignItems: 'center'}}>
                                    <BuildIcon fontSize='large' />
                                    <Typography variant='h6'>Funciones</Typography>
                                </Stack>
                            </AccordionSummary>
                            <AccordionDetails>
                                <Stack sx={{marginLeft: '12px', maxHeight: '200px', overflowY: 'auto'}}>
                                    {
                                        departamento!.funciones.map((funcion, index) => {
                                            return (
                                                <Box key={'funcion' + index}>
                                                    <Stack direction='row' sx={{alignItems: 'center'}}>
                                                        <ArrowForwardIosIcon /> 
                                                        <Typography variant='caption' key={'funcion' + index}>{ funcion }</Typography>
                                                    </Stack>
                                                    <Divider sx={{margin: '2px 0'}} />
                                                </Box>
                                            )    
                                        })           
                                    }     
                                </Stack>
                            </AccordionDetails>
                        </Accordion>

                        <Accordion elevation={8} sx={{margin: '6px 0'}}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1a-content"
                                id="panel2a-header"
                            >
                                <Stack direction='row' spacing={2} sx={{alignItems: 'center'}}>
                                    <EngineeringIcon fontSize='large' />
                                    <Typography variant='h6'>Trabajadores</Typography>
                                </Stack>
                            </AccordionSummary>
                            <AccordionDetails>
                                <Stack sx={{marginLeft: '12px'}}>
                                    {
                                        departamento!.trabajadores.map(({nombre, puesto}, index) => {
                                            return(
                                                <Stack key={'trabajador' + index} direction='row' sx={{alignItems: 'center'}}>
                                                    <AccountBoxIcon /> 
                                                    <Typography variant='caption' key={'trabajador' + index}>{nombre + ' (' + puesto + ')'}</Typography>
                                                </Stack>
                                            )
                                        })
                                    } 
                                </Stack>
                            </AccordionDetails>
                        </Accordion>                
                    
                    
                        <Accordion elevation={8} sx={{margin: '6px 0'}}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1a-content"
                                id="panel3a-header"
                            >
                                <Stack direction='row' spacing={2} sx={{alignItems: 'center'}}>
                                    <LocalLibraryIcon fontSize='large' />
                                    <Typography variant='h6'>Contactos</Typography>
                                </Stack>
                            </AccordionSummary>
                            <AccordionDetails>
                                <Stack sx={{marginLeft: '12px', maxHeight: '200px', overflowY: 'auto'}}>
                                {
                                    departamento!.contactos.map(({tipo, contacto, descripcion}, index) => {
                                        return(
                                            <Stack key={'contacto' + index} direction='row' sx={{alignItems: 'center'}}>
                                                {tipo === 'email' ? <MailIcon /> : <LocalPhoneIcon />}
                                                <Typography variant='caption' key={'contacto' + index}>{contacto + (descripcion !== '' ? ' (' + descripcion + ')': '')}</Typography>
                                            </Stack>
                                        )
                                    })
                                }   
                                </Stack>
                            </AccordionDetails>
                        </Accordion>  
                    
                    
                </Stack>
                </Grid>

            </Grid>

            
            


        </Stack>
    )
}
