import React, { useState } from 'react'
import { Grid, Paper, Stack, Typography, Divider } from '@mui/material'
import { Departamento, listaDeptos } from '../../models/Departamento';
import { styled } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import './MuniDeptoHub.css';

const MyPaper = styled(Paper)(({ theme }) => `  
    
        border: 2px solid #0000002e;        
        overflow: hidden;                
        -webkit-touch-callout: none; 
        -webkit-user-select: none; /* Safari */
        -khtml-user-select: none; /* Konqueror HTML */
        -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
        user-select: none; 
    

    :hover {
        border: 2px solid ${theme.palette.primary.main};
        cursor: pointer;
    }
    `
);


export const MuniDeptoHub = () => {
    
    const [deptos, setDeptos] = useState<( Departamento & { elevation : number, selected: boolean } ) [] >(listaDeptos.map(deptoCurrent => ({...deptoCurrent, elevation: 4, selected: false})));
    const navigate = useNavigate();

    const setElevation = (elvation: number, index : number) => {
        deptos[index].elevation = elvation;
        setDeptos([...deptos]);
    }

    const goToDetails = (index : string) => {
        navigate(`/Departamento/${index}`);
    }

    return (
        <>
            <Typography variant='h4' textAlign='center'>DEPARTAMENTOS Y UNIDADES DENTRO DE LA MUNICIPALIDAD</Typography>
            <Divider variant='middle'/>

            
            <Grid container spacing={2}  sx={{ width: '1200px', maxWidth: '95%', margin: 'auto'}}>
                {
                    deptos.map(({nombre, abreviatura, img, descripcion, elevation, selected}, index) => {
                        return (
                            <Grid key={abreviatura} item xs={12} md={4}>
                                <MyPaper                              
                                    onMouseOver={() => setElevation(12, index)} 
                                    onMouseOut={() => setElevation(4, index)} 
                                    onClick={() => goToDetails(abreviatura)}
                                    elevation={selected ? 10 : elevation}
                                    sx={{borderColor: selected ? 'primary.main' : '#0000002e',

                                    }}
                                    >
                                    <Stack direction='row' sx={{maxHeight: '90px'}}>
                                        <img src={img} alt={nombre} style={{ maxHeight: '90px', borderRight: '1px solid #0000002e' }} />
                                        <Stack p={1} sx={{overflow: 'hidden'}}>
                                            <Typography sx={{fontWeight: 'bold'}}>
                                                {nombre}
                                            </Typography>
                                            <Typography 
                                            variant='caption' 
                                            color='text.disabled'
                                            sx={{textAlign: 'justify', lineHeight: '1.1',
                                            marginBottom: '0'}} paragraph>
                                                {descripcion}
                                            </Typography>
                                        </Stack>
                                    </Stack>
                                </MyPaper>                            
                            </Grid>
                        )
                    })
                    
                }            
            </Grid>

        </>
  )
}
