import React, { useState, useEffect } from 'react';
import { Routes, Route, useLocation} from "react-router-dom";
import { Home } from './pages/Home';
import { Departamentos } from './pages/Departamentos';
import { Construction } from './pages/Construction';
import { AppBar, IconButton, Toolbar, Box } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { MuniNavBar } from './components/MuniNavBar';
import { MuniDeptoPresentation } from './components/Departamentos/MuniDeptoPresentation';
import { MuniFooter } from './components/MuniFooter';
import { Patrimonio } from './components/Home/InfoPages/Patrimonio';


function App() {

  const location = useLocation();
  const [open, setOpen] = useState(false);
  const [completeFooter, setCompleteFooter] = useState(true);

  const handleOpenChange = () => {
      setOpen(!open);
  }

  useEffect(() => {
    location.pathname === '/'
    ? setCompleteFooter(true)
    : setCompleteFooter(false);
  }, [location]);

    return (
      <div className="App">

        <AppBar position="fixed" sx={{bgcolor: 'background.paper'}}>
          <Toolbar variant="dense">
            <IconButton edge="start" aria-label="menu" sx={{ mr: 2 }} onClick={handleOpenChange}>
              <MenuIcon />
            </IconButton>
          </Toolbar>
        </AppBar>

        <MuniNavBar open={open} handleOpenChange={handleOpenChange} />
        <div style={{
                background: 'linear-gradient(90deg, rgba(48,129,0,1) 0%, rgba(255,255,255,1) 50%, rgba(255,250,10,1) 100%)',
                height: '56px',
          }}
        >
        </div> 

        <Box sx={{minHeight: completeFooter ? 'calc(100vh - 56px)' : 'calc(100vh - 73px)'}}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="Departamentos" element={<Departamentos />} />
            <Route path="/Departamento/:deptoID" element={<MuniDeptoPresentation />} />

            <Route path='/Patrimonio' element={<Patrimonio />} />

            <Route path='*' element={<Construction />} />
          </Routes>
        </Box>
          
        <MuniFooter complete={completeFooter} />
      </div>
    );
}

export default App;
